const app = getApp()
// 获取公共域名地址
import serverUrl from '@/common/serveUrl.js';
export default {
	data() {
		return {
			globalData: {},
			isSelect:false,
			addressList:[],
			imgUrl:this.$imgUrl
		};
	},
	onLoad(option) {
		var that = this;
		this.globalData = app.globalData
		this.getDetailsData()
	},
	methods:{
		getDetailsData() {
		    let _this = this;
			const url = serverUrl.serverUrl;
		    uni.request({
				url:"https://wx.zzsnqyjxh.org/test/app/appAddress/query?userId=1",
				// data: {
				// // // 	// userId: _this.data.globalData.userInfo.id
				// 	// userId: this.globalData.userInfo.id
				// 	userId:1
				// },
				method:"POST",
				success: res => {
					// console.log(res)
					if (res.data.code == 200) {
						_this.addressList = res.data.data	
					}
				}
		    })
		},
		edit(row){
			uni.setStorage({
				key:'address',
				data:row,
				success() {
					uni.navigateTo({
						url:"/pages/address/addressManage?type=edit"
					})
				}
			});
			
		},
		add(){
			uni.navigateTo({
				url:"/pages/address/addressManage?type=add"
			})
		},
		select(row){
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if(!this.isSelect){
				return ;
			}
			uni.setStorage({
				key:'selectAddress',
				data:row,
				success() {
					uni.navigateBack();
				}
			})
		}
	}
}
